}
.remember-password input[type="checkbox"] {
  display: none;


}

.remember-password label {
    position: relative;
    display: inline-block;
    padding-left: 25px;

}

.remember-password label::before,
.remember-password label::after {
    position: absolute;
    content: "";
    display: inline-block;
    cursor: pointer;

}

.remember-password label::before{
    height: 16px;
    width: 16px;
    border: 1px solid;
    left: 0;
    top: 1px;

}

.remember-password label::after {
    height: 5px;
    width: 9px;
    border-left: 2px solid;
    border-bottom: 2px solid;
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
    left: 4px;
    top: 5px;

}


.remember-password input[type="checkbox"] + label::after {
    content: none;
}


.remember-password input[type="checkbox"]:checked + label::after {
    content: "";
}

.remember-password input[type="checkbox"]:focus + label::before {
    outline: rgb(59, 153, 252) auto 5px;
}

